import { WebGLRenderer, Scene, PerspectiveCamera, Vector3, MOUSE } from "three";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { addGeoObject, initSVGObject } from "./svgGraphic";
import Stats from "three/addons/libs/stats.module.js";

export class ThreeEngine {
    container = null; //  DOM
    scene = null; // 
    constructor(container) {
        // 
        let renderer = new WebGLRenderer({
            antialias: true, // 
        });
        container.appendChild(renderer.domElement); // dom
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(container.offsetWidth, container.offsetHeight, true);
        let scene = new Scene(); // 
        // 
        let camera = new PerspectiveCamera(
            45,
            container.offsetWidth / container.offsetHeight,
            1,
            1000
        );
        camera.position.set(6, 6, 8); // 
        camera.lookAt(new Vector3(0, 0, 0)); // 
        camera.up = new Vector3(0, 1, 0); // 
        // camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();

        const group = new THREE.Group();
        scene.add(group);


        let selectedObject = null;
        const raycaster = new THREE.Raycaster();
        const pointer = new THREE.Vector2();

        this.container = container;
        this.scene = scene;
        this.group = group;

        let orbitControls = new OrbitControls(camera, renderer.domElement);

        let stats = new Stats();
        container.appendChild(stats.dom);

        renderer.render(scene, camera); // 
        renderer.setClearColor(0x1f2937, 1); //
        let onWindowResize = () => {
            // console.log('dede',window)
            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();
            renderer.setSize(window.innerWidth, window.innerHeight);
        };
        window.addEventListener("resize", onWindowResize);

        let onPointerMove = (event) => {


            if (selectedObject) {

                selectedObject.scale.set(1, 1, 1)
                selectedObject = null;
            }

            pointer.x = (event.clientX / window.innerWidth) * 2 - 1;
            pointer.y = - (event.clientY / window.innerHeight) * 2 + 1;

            raycaster.setFromCamera(pointer, camera);

            const intersects = raycaster.intersectObject(group, true);

            if (intersects.length > 0) {

                const res = intersects.filter(function (res) {
                    return res && res.object;
                })[0];

                if (res && res.object) {
                    console.log('dedede', res)

                    selectedObject = res.object;
                    selectedObject.scale.set(2, 2, 2)

                }

            }
        }

        document.addEventListener('pointermove', onPointerMove);

        // threejs
        let animate = () => {
            renderer.render(scene, camera); // 
            requestAnimationFrame(animate);
            stats.update();
        };
        animate();
    }
    /**
     * 
     * @param  {...any} object 
     */
    addObject(...object) {
        object.forEach((elem) => {
            this.scene.add(elem); // 
        });
    }
    addSvg() {
        const { arcs, pieSvgDataUri } = initSVGObject();
        addGeoObject(this.group, arcs, pieSvgDataUri);
    }
}
